.play-list-wapper {
    width: 100%;
    max-width: 850px;
    height: 350px;
    margin: 50px auto;
    overflow: hidden;
    display: flex;
}

.preview {
    box-sizing: border-box;
    width: 70%;
}

.preview .local-video {
    width: 100%;
    height: 100%;
}

.list {
    margin-left: 2%;
    width: 28%;
    height: 100%;
    overflow-y: auto;
    border: 1px solid #eee;
    box-sizing: border-box;
}

.list ul {
    list-style: none;
    margin: 0;
    padding: 0;
}
